@mixin anim($class, $transStart, $animDelay:0.1s, $transEnd:none ) {
  #{$class} {
    opacity: 0;
    transition:all 320ms ease-out;
    transform:#{$transStart};
    .loaded & {
      opacity:1;
      transform:#{$transEnd};
      transition-delay:#{$animDelay};
    }
  }
}
@mixin anim-items($class, $transStart, $animDelay:0.1s, $n:50) {
  @include anim($class, $transStart, $animDelay);
  @for $i from 1 through $n {
    .loaded #{$class}:nth-child(#{$i}) {
      transition-delay:($animDelay + ($i - 1) * 0.1s);
    }
  }
}

